.new-destinations__alphabet-country-block {
  width: 220px;
  margin: 0 auto;
  //position: relative;
  //bottom: 120px;
  position: absolute;
  top: 2663px;
  left: 50px;
  z-index: 1;
  display: none;

  &--modal-show {
    display: block;
  }

  @media (min-width: $tablet-width) {
    display: none;
    flex-wrap: wrap;
    width: 575px;
    padding-bottom: 30px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    top: 3320px;
    left: 122px;

    //background-color: $color-white;
    &--modal-show {
      display: block;
    }
  }

  @media (min-width: $desktop-width) {
    display: none;
    flex-wrap: wrap;
    width: 730px;
    justify-content: space-between;
    box-shadow: 0 20px 50px rgba(29, 46, 91, 0.1);
    padding-bottom: 0;

    &--modal-show {
      display: flex;
      top: 2850px;
      left: 500px;
    }
  }
}

.new-destinations__alphabet-country-block .new-destinations__country-a {
  border: 1px solid $color-grey-light-3;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: none;
  margin-top: 1px;
  padding-top: 20px;
  padding-bottom: 20px;
  font-weight: 400;
  margin-bottom: 0;
  width: 100%;

  @media (min-width: $tablet-width) {
    width: 206px;
    padding: 0;
    border-radius: 0;
    margin-left: auto;
    margin-top: 34px;
    border: none;
    background-color: $color-white;
  }
}

.new-destinations__alphabet-country-block .new-destinations__country-selection {
  margin-bottom: 0;
  color: $color-white;
  background-color: $color-dark-blue-2;
  border: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  &::after {
    display: none;
  }

  @media (min-width: $tablet-width) {
    width: 369px;
    margin-left: 0;
    order: -1;
  }

  @media (min-width: $desktop-width) {
    width: 730px;
    //margin-left: 0;
    //order: -1;
  }
}

.new-destinations__alphabet-country-block .new-destinations__action {
  border: none;
}

.new-destinations .form-table {
  margin: 0;
  background-color: $color-white;

  @media (min-width: $tablet-width) {
    margin-left: 26px;
    background-color: $color-white;
  }

  @media (min-width: $desktop-width) {
    margin: 0;
  }
}

.new-destinations .country-a {
  @media (min-width: $desktop-width) {
    //margin-right: auto;
  }
}

.new-destinations__alphabet-country-block .table-country-wrapper {
  display: flex;
  flex-wrap: wrap;
  box-shadow: 0 20px 50px rgba(29, 46, 91, 0.1);

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: $color-white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}
